/**
 * 
 */
import {parseTime} from '@/utils'

const filters = {
  //  yyyy-MM-dd mm:hh:ss
  DateTime: function (time) {
    return parseTime(time)
  },
  // 
  styleStatus: function (status) {
    let res = ''
    switch (status) {
      case 0:
        res = ''
        break
      case 1:
        res = ''
        break
    }
    return res
  },
  styleTypeStatus: function (status) {
    let res = ''
    switch (status) {
      case 0:
        res = ''
        break
      case 1:
        res = ''
        break
    }
    return res
  }
}

export default filters
