//
const state = {
  cartItems: JSON.parse(localStorage.getItem('cartItems')) || [],
  sheet: false
}
const getters = {
  getSheetStatus: (state) => {//
    return state.sheet
  },
  getCartItems(state) {//
    return state.cartItems
  },
  getCartTotal(state){ //
    let count = 0
    state.cartItems.forEach(item => {
      count += item.count
    })
    return count
  },
  getCartItemTotal: (state) => (id) => {//
    let item = state.cartItems.find(item => item.id == id)
    return item.count
  },
  calcTotalPrice: (state) => (idArr) => {
    let totalPrice = 0
    idArr.forEach(id => {
     let item = state.cartItems.find(item => item.id == id)
      totalPrice += item.price * item.count
    })
    return totalPrice
  }
}

const actions = {}

const mutations = {
  setSheetStatus: (state) => {//
    state.sheet = !state.sheet
  },
  addGoodsToCart(state, obj) {//
    let cartItem = state.cartItems.find(item => item.id === obj.id)
    if (cartItem) {
      cartItem.count += obj.count
      cartItem.selected = obj.selected
      console.log(cartItem)
    } else {
      state.cartItems.push(obj)
    }
    localStorage.setItem('cartItems',JSON.stringify(state.cartItems))
  },
  addQuantity(state, id) {//
    let item = state.cartItems.find(item => item.id == id)
    item.count++
    localStorage.setItem('cartItems',JSON.stringify(state.cartItems))
  },
  subtractQuantity(state, id) {//
    let item = state.cartItems.find(item => item.id == id)
    if (item.count > 1) {
      item.count--
      localStorage.setItem('cartItems',JSON.stringify(state.cartItems))
    }
  },
  setSelectedStatus(state,idArr){//
    state.cartItems.forEach(item => {//false
      item.selected = false
    })
    idArr.forEach(id => {//idselectedtrue
     let item = state.cartItems.find(item => item.id == id)
      item.selected = true
    })
    localStorage.setItem('cartItems',JSON.stringify(state.cartItems))
  },
  deleteGoodsFromCart(state,id){//
    let itemIndex = state.cartItems.findIndex(item => item.id == id)
    if (itemIndex != -1){
      state.cartItems.splice(itemIndex,1)
      localStorage.setItem('cartItems',JSON.stringify(state.cartItems))
    }
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}
